@import '../styles/variables';
@import '../styles/mixins';

.app-main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  max-width: $main-max-width;
  margin: auto;
  background-color: $main-bg-color;

  &__submenu {
    display: none;

    @include mobile {
      display: block;
      padding-top: 1em;

      ul li:not(:last-child) {
        border-right: 1px solid $color-black;
      }
    }
  }

  &__page-container {
    width: 100%;
    height: 100%;
    padding: 1em 4em 1em 4em;

    @include mobile {
      padding: 0 1em;
    }

    a {
      font-weight: bold;
    }

    p + p {
      margin-bottom: 1em;
    }

    > section {
      position: relative;
      height: 100%;
    }

    article {
      padding-bottom: 1em;
    }

    .footnotes {
      position: absolute;
      bottom: 0;
      padding-top: 1em;
      font-size: small;

      @include mobile {
        position: initial;
      }
    }
  }
}
